import React, { Component } from 'react';
import Button from '../components/Button/Button';
import Display from '../components/Display/Display';
import './calculator.css'

const initialState = {
    displayValue: '0',
    clearDisplay: false,
    operation: null,
    values: [0, 0],
    current: 0
}

class Calculator extends Component {

    constructor(props) {
        super(props)
        this.clearMemory = this.clearMemory.bind(this)
        this.setOperarion = this.setOperarion.bind(this)
        this.addDigit = this.addDigit.bind(this)
        this.state = { ...initialState }
    }

    clearMemory() {
        this.setState({ ...initialState })
    }

    setOperarion(operation) {
        if (this.state.current === 0) {
            this.setState({ operation, current: 1, clearDisplay: true })
        } else {
            const equals = operation === "=";
            const currentOperation = this.state.operation

            const values = [...this.state.values]

            if (currentOperation === '/' && values[1] === 0) {
                return
            }

            try {
                values[0] = eval(`${this.state.values[0]} ${currentOperation} ${this.state.values[1]}`)
            } catch (e) {
                values[0] = this.state.values[0]
            }

            values[1] = 0;
            this.setState({
                displayValue: values[0].toString(),
                operation: equals ? null : operation,
                current: 1,
                clearDisplay: true,
                values
            })

        }
    }

    addDigit(n) {
        if (n === '.' && this.state.displayValue.includes('.') && this.state.clearDisplay !== true) {
            return
        }
        const clearDisplay = this.state.displayValue === '0'
            || this.state.clearDisplay;
        const currentValue = clearDisplay ? '' : this.state.displayValue;
        const displayValue = currentValue + n;
        this.setState({ displayValue, clearDisplay: false })

        if (n !== '.') {
            const i = this.state.current;
            const newValue = parseFloat(displayValue);
            const values = this.state.values
            values[i] = newValue
            this.setState({ values })
        }
    }

    render() {
        return (
            <div className="calculator">
                <Display value={this.state.displayValue} />
                <Button label="A/C" click={this.clearMemory} triple />
                <Button label="/" click={this.setOperarion} operation />
                <Button label="7" click={this.addDigit} />
                <Button label="8" click={this.addDigit} />
                <Button label="9" click={this.addDigit} />
                <Button label="*" click={this.setOperarion} operation />
                <Button label="4" click={this.addDigit} />
                <Button label="5" click={this.addDigit} />
                <Button label="6" click={this.addDigit} />
                <Button label="-" click={this.setOperarion} operation />
                <Button label="1" click={this.addDigit} />
                <Button label="2" click={this.addDigit} />
                <Button label="3" click={this.addDigit} />
                <Button label="+" click={this.setOperarion} operation />
                <Button label="0" click={this.addDigit} double />
                <Button label="." click={this.addDigit} />
                <Button label="=" click={this.setOperarion} operation />
            </div>
        );
    }
}

export default Calculator;